<template>
  <div>
    <div
      class="
        widgetContainer widgetContainer--center
        bg-white
        widgetContainer--full-screen
        verifying
        appStatus appStatus--verifying
      ">
      <div class="widgetContainer__body">
        <LottieAnimation animation-name="searching" reference="business-status-review-1" :loop="true" />
        <p class="title">
          {{ $t('esign_callbackTitle') }}
        </p>
        <div class="widgetContainer__footer--fixed">
          <el-button
            :disabled="!isRetryCountReached"
            type="primary"
            class="welcome__get-started el-button__brand brand"
            @click="checkStatus(false)">
            {{ $t('esign_ctaText') }}
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';
import loginMixins from '@m/user/mixins/login';
import { setEnv } from '@/utils/env';
import LottieAnimation from '@/components/LottieAnimation.vue';

const RETRY_COUNT = 3;

export default {
  mixins: [loginMixins],
  components: {
    LottieAnimation
  },
  created() {
    try {
      const tokens = window.sessionStorage.getItem('user_auth_tokens');
      const appEnv = window.sessionStorage.getItem('app_env');
      const businessId = window.sessionStorage.getItem('business_id');
      if(tokens && appEnv && businessId) {
        setEnv(appEnv);
        const tokenJson = JSON.parse(tokens);
        this.setUserAuthToken(tokenJson);
        this.$http.defaults.headers.common.AUTHORIZATION =
                'Bearer ' + tokenJson.accessToken;

        this.verifyToken(businessId);
        window.sessionStorage.removeItem('user_auth_tokens');
        window.sessionStorage.removeItem('app_env');
        window.sessionStorage.removeItem('business_id');
      } else {
        this.goToWelcomeScreen();
      }
    } catch {
      this.goToWelcomeScreen();
    }
  },
  computed: {
    ...mapGetters('kyb', ['getSelectedBusiness']),
  },
  data() {
    return {
      retryCount: 0,
      isRetryCountReached: false,
    }
  },
  methods: {
    ...mapActions('user', ['getPerson']),
    ...mapMutations('user', ['setUserAuthToken']),
    ...mapMutations('kyb', ['setSelectedBusiness']),
    ...mapActions('kyb', ['submitKyb', 'fetchBusiness', 'getOwnershipDisclosure']),
    verifyToken(businessId) {
      const loader = this.showLoader();

      Promise.all([this.getPerson(), this.fetchBusiness(businessId)])
        // eslint-disable-next-line no-unused-vars
        .then(([_, business]) => {
          this.setSelectedBusiness(business);
          setTimeout(() => {
            this.loginSuccess(() => {
              const event = this.$route.query.event;
              if(event === 'signing_complete') {
                this.checkStatus();
                
              } else {
                const messageMap = {
                  access_code_failed: 'Something went wrong, please try again.',
                  cancel: 'Something went wrong, please try again.',
                  decline: 'Something went wrong, please try again.',
                  exception: 'Something went wrong, please try again.',
                  fax_pending: 'Something went wrong, please try again.',
                  id_check_failed: 'Something went wrong, please try again.',
                  session_timeout: 'Session timeout, please try again.',
                  ttl_expired: 'Session timeout, please try again.',
                }

                this.apiErrorMessage(messageMap[event] || 'Something went wrong, please try again.');
                this.$router.push('/kyb/ownersList');
              }
            });
            
          }, 2000);
        })
        .catch((e) => {
          this.apiErrorMessage(e);
          console.log(e);
        })
        .finally(() => {
          loader.close();
        });
    },
    checkStatus(isRetryCall = true) {
      if ((this.retryCount < RETRY_COUNT || !isRetryCall)) {
        this.getOwnershipDisclosure(this.getSelectedBusiness.id)
          .then((res) => {
            if(res.status === 'completed') {
              this.startKYB();
            } else if(isRetryCall) {
              setTimeout(() => {
                this.retryCount += 1;
                this.checkStatus();
              }, 3000);
            }
          }).catch((e) => {
            if (e.code === 'EC_BUSINESS_AGREEMENT_NOT_FOUND' && isRetryCall) {
              setTimeout(() => {
                this.retryCount += 1;
              }, 3000);
            }
          });
      } else if(this.retryCount >= RETRY_COUNT) {
        this.isRetryCountReached = true
      }
    },
    startKYB() {
      const business = this.getSelectedBusiness;
      if (business && business.id) {
        const loader = this.showLoader();
        this.submitKyb(business.id)
          .then(() => {
            setTimeout(() => {
              this.$router.push('/kyb/businessStatus');
            }, 0);
          })
          .catch((e) => {
            console.log(e);
            this.apiErrorMessage(e);
          })
          .finally(() => {
            loader.close();
          });
      }
    }
  }
}
</script>